import floatEqual from 'float-equal';
import clamp from './clamp';
import dot from './dot';

export default function closestPtSegmentSegment(p1x, p1y, q1x, q1y, p2x, p2y, q2x, q2y) {
    const d1x = q1x - p1x;
    const d1y = q1y - p1y;
    const d2x = q2x - p2x;
    const d2y = q2y - p2y;
    const rx = p1x - p2x;
    const ry = p1y - p2y;
    const a = dot(d1x, d1y, d1x, d1y);
    const e = dot(d2x, d2y, d2x, d2y);
    const f = dot(d2x, d2y, rx, ry);
    let s, t;
    if (floatEqual(a, 0.0) && floatEqual(e, 0.0)) {
        return [p1x, p1y, p2x, p2y];
    }
    if (floatEqual(a, 0.0)) {
        s = 0.0;
        t = f / e;
        t = clamp(t, 0.0, 1.0);
    } else {
        const c = dot(d1x, d1y, rx, ry);
        if (floatEqual(e, 0.0)) {
            t = 0.0;
            s = clamp(-c / a, 0.0, 1.0);
        } else {
            const b = dot(d1x, d1y, d2x, d2y);
            const denom = a * e - b * b;
            if (!floatEqual(denom, 0.0)) {
                s = clamp((b * f - c * e) / denom, 0.0, 1.0);
            } else s = 0.0;
            const tnom = b * s + f;
            if (tnom < 0.0) {
                t = 0.0;
                s = clamp(-c / a, 0.0, 1.0);
            } else if (tnom > e) {
                t = 1.0;
                s = clamp((b - c) / a, 0.0, 1.0);
            } else {
                t = tnom / e;
            }
        }
    }
    return [p1x + d1x * s, p1y + d1y * s, p2x + d2x * t, p2y + d2y * t];
}
