const PI2 = Math.PI * 2.0;

export const randomInteger = (min, max) => {
    let rand = min + Math.random() * (max + 1 - min);
    rand = Math.floor(rand);
    return rand;
};

export const toInt = value => value | 0;

export const getAngle = (x, y) => {
    let angle = Math.atan(Math.abs(x / y));

    // calculate angle, value is 0-360
    if (x > 0 && y < 0) {
        angle = Math.PI - angle;
    } else if (x < 0 && y < 0) {
        angle = Math.PI + angle;
    } else if (x < 0 && y > 0) {
        angle = Math.PI * 2 - angle;
    }

    return angle;
};

export const getDegAngle = (cx, cy, ex, ey) => {
    const dy = ey - cy;
    const dx = ex - cx;
    let theta = Math.atan2(dy, dx);
    theta *= 180 / Math.PI; // rads to degs, range (-180, 180]
    if (theta < 0) {
        theta = theta * -1 + 180; // Turn into 0-360
    }
    return theta;
};

export const rotatePoint = (pivotX, pivotY, pointX, pointY, sin, cos) => {
    const deltaX = pointX - pivotX;
    const deltaY = pointY - pivotY;
    // Rotate clockwise, angle in radians
    return {
        x: toInt(cos * deltaX - sin * deltaY + pivotX),
        y: toInt(sin * deltaX + cos * deltaY + pivotY)
    };
};

export const randomInCircle = (x, y, radius, pointSize) => {
    const t = PI2 * Math.random();
    const u = Math.random() + Math.random();
    const r = u > 1 ? 2 - u : u;
    const s = r * (radius - 0.5 * pointSize);
    return {
        x: Math.trunc(Math.cos(t) * s + x),
        y: Math.trunc(Math.sin(t) * s + y)
    };
};

export const cartesian2Polar = (x, y) => {
    return {
        radius: Math.sqrt(x * x + y * y),
        angle: Math.atan2(y, x)
    };
};

export const polar2Cartesian = (r, theta) => {
    return {
        x: r * Math.cos(theta),
        y: r * Math.sin(theta)
    };
};

export const lerp = (a1, a2, t) => a1 + (a2 - a1) * t;

export const flatten = arr => [].concat(...arr);

export const arcMiddlePoint = (cx, cy, cr, a1, a2) => {
    const a = (a1 + (a2 > a1 ? a2 : a2 + Math.PI * 2.0)) * 0.5;
    return {
        x: cx + cr * Math.cos(a),
        y: cy + cr * Math.sin(a)
    };
};

export const signedDeltaAngle = (to, from) => Math.atan2(Math.sin(to - from), Math.cos(to - from));

export const sqSegLength = (toX, toY, fromX, fromY) => {
    const dx = toX - fromX;
    const dy = toY - fromY;
    return dx * dx + dy * dy;
};

export const oscillate = (vMin, vMax, t) => {
    const halfRange = ((vMax - vMin) / vMax) * 0.5;
    return (Math.sin(t) * halfRange + (1.0 - halfRange)) * vMax;
};

export const insideAngle = (target, facing, half) => {
    const dot = Math.cos(facing) * Math.cos(target) + Math.sin(facing) * Math.sin(target);
    const angle = Math.acos(dot);
    return angle <= half;
};

export const limit = (value, minVal, maxVal) => Math.max(minVal, Math.min(maxVal, value));

export const wrap = (value, min, max) => {
    const range = max - min;
    return min + ((((value - min) % range) + range) % range);
};

export const normalizeAngle = angle => {
    const a = angle % PI2;
    if (a >= 0) {
        return a;
    }
    return a + 2 * Math.PI;
};

export const reverseAngle = angle => normalizeAngle(angle + Math.PI);

export const rotateTo = (currentAngle, targetAngle, lp = 0.05) => {
    if (currentAngle === targetAngle) {
        return currentAngle;
    }
    let ca = currentAngle;
    let ta = targetAngle;
    const da = Math.abs(ta - ca);

    if (da <= lp || da >= PI2 - lp) {
        ca = ta;
    } else {
        if (da > Math.PI) {
            if (targetAngle < currentAngle) {
                ta += PI2;
            } else {
                ta -= PI2;
            }
        }
        if (targetAngle > currentAngle) {
            ca += lp;
        } else if (targetAngle < currentAngle) {
            ca -= lp;
        }
    }
    return ca;
};

export const wrapAngle = angle => wrap(angle, -Math.PI, Math.PI);

export const shortestBetween = (angle1, angle2) => {
    const a1 = wrapAngle(angle1);
    const a2 = wrapAngle(angle2);
    const difference = a2 - a1;

    if (difference === 0) {
        return 0;
    }
    const times = Math.floor((difference - -Math.PI) / PI2);
    return difference - times * PI2;
};

export const isAngleBetween = (a1, a2, check) => {
    const d = a1 - check;
    const diff = wrapAngle(d);
    return diff < a2 && diff > -a2;
};

export const reflect = (v, n) => {
    const d = v[0] * n[0] + v[1] * n[1]; // dot(v, n)
    return [v[0] - 2.0 * d * n[0], v[1] - 2.0 * d * n[1]];
};

export const randomSign = () => (Math.random() < 0.5 ? -1 : 1);

export const randomVal = (a, b) => (Math.random() < 0.5 ? a : b);
