const Fullscreen = {
    available: false,
    cancel: '',
    keyboard: false,
    request: ''
};

function init() {
    const suffix1 = 'Fullscreen';
    const suffix2 = 'FullScreen';

    const fs = [
        `request${suffix1}`,
        `request${suffix2}`,
        `webkitRequest${suffix1}`,
        `webkitRequest${suffix2}`,
        `msRequest${suffix1}`,
        `msRequest${suffix2}`,
        `mozRequest${suffix2}`,
        `mozRequest${suffix1}`
    ];
    for (let i = 0; i < fs.length; i++) {
        if (document.documentElement[fs[i]]) {
            Fullscreen.available = true;
            Fullscreen.request = fs[i];
            break;
        }
    }
    const cfs = [
        `cancel${suffix2}`,
        `exit${suffix1}`,
        `webkitCancel${suffix2}`,
        `webkitExit${suffix1}`,
        `msCancel${suffix2}`,
        `msExit${suffix1}`,
        `mozCancel${suffix2}`,
        `mozExit${suffix1}`
    ];
    if (Fullscreen.available) {
        for (let i = 0; i < cfs.length; i++) {
            if (document[cfs[i]]) {
                Fullscreen.cancel = cfs[i];
                break;
            }
        }
    }
    if (
        window.Element &&
        Element.ALLOW_KEYBOARD_INPUT &&
        !/ Version\/5\.1(?:\.\d+)? Safari\//.test(navigator.userAgent)
    ) {
        Fullscreen.keyboard = true;
    }
    Object.defineProperty(Fullscreen, 'active', {
        get() {
            return !!(
                document.fullscreenElement ||
                document.webkitFullscreenElement ||
                document.mozFullScreenElement ||
                document.msFullscreenElement
            );
        }
    });
    return Fullscreen;
}

export default init();
