import OS from './OS';

const Browser = {
    chrome: false,
    chromeVersion: 0,
    edge: false,
    firefox: false,
    firefoxVersion: 0,
    ie: false,
    ieVersion: 0,
    mobileSafari: false,
    opera: false,
    safari: false,
    safariVersion: 0,
    silk: false,
    trident: false,
    tridentVersion: 0
};

function init() {
    const ua = navigator.userAgent;
    if (/Edge\/\d+/.test(ua)) {
        Browser.edge = true;
    } else if (/Chrome\/(\d+)/.test(ua) && !OS.windowsPhone) {
        Browser.chrome = true;
        Browser.chromeVersion = parseInt(RegExp.$1, 10);
    } else if (/Firefox\D+(\d+)/.test(ua)) {
        Browser.firefox = true;
        Browser.firefoxVersion = parseInt(RegExp.$1, 10);
    } else if (/AppleWebKit/.test(ua) && OS.iOS) {
        Browser.mobileSafari = true;
    } else if (/MSIE (\d+\.\d+);/.test(ua)) {
        Browser.ie = true;
        Browser.ieVersion = parseInt(RegExp.$1, 10);
    } else if (/Opera/.test(ua)) {
        Browser.opera = true;
    } else if (/Safari/.test(ua) && !OS.windowsPhone) {
        Browser.safari = true;
    } else if (/Trident\/(\d+\.\d+)(.*)rv:(\d+\.\d+)/.test(ua)) {
        Browser.ie = true;
        Browser.trident = true;
        Browser.tridentVersion = parseInt(RegExp.$1, 10);
        Browser.ieVersion = parseInt(RegExp.$3, 10);
    }
    if (/Silk/.test(ua)) {
        Browser.silk = true;
    }
    return Browser;
}

export default init();
