import './libs/polyfills/console';
import './libs/polyfills/Math.trunc';
import './libs/polyfills/AudioContextMonkeyPatch';
import Stats from 'stats-js';
import MicroModal from 'micromodal';
import { parse } from 'qs';
import angleBetweenCoords from './libs/angleBetweenCoords';
import { INIT_FOODS_COUNT, INIT_UNITS_COUNT, MAP_RADIUS } from './common/config';
import { MSSEC } from './libs/constants';
import GameMap from './framework/GameMap';
// import VisibilityHandler from './framework/VisibilityHandler';
import Events from './framework/events';
import OS from './framework/device/OS';
import Browser from './framework/device/Browser';
import Audio from './framework/device/Audio';
import Fullscreen from './framework/device/Fullscreen';
import Input from './framework/device/Input';
import Features from './framework/device/Features';
import CanvasFeatures from './framework/device/CanvasFeatures';
import Video from './framework/device/Video';
import Deltaframe from '../node_modules/deltaframe/deltaframe';

const stats = new Stats();
const { log, info } = console;
MicroModal.init({
    onShow: modal => info(`${modal.id} is shown`), // [1]
    onClose: modal => info(`${modal.id} is hidden`), // [2]
    disableScroll: true, // [5]
    disableFocus: false, // [6]
    awaitCloseAnimation: false, // [7]
    debugMode: true // [8]
});

MicroModal.show('modal-1');
const search = parse(window.location.search, {
    ignoreQueryPrefix: true
});
log(OS);
log(Browser);
log(Audio);
log(Fullscreen);
log(Input);
log(Features);
log(CanvasFeatures);
log(Video);

const numUnits = parseInt(search.units, 10);
const numFoods = parseInt(search.foods, 10);
const radius = parseInt(search.radius, 10);

stats.showPanel(0);
document.body.appendChild(stats.dom);
const canvas = document.getElementById('cas');

// window's width and height
const vWidth = window.innerWidth;
const vHeight = window.innerHeight;

const KeyCodes = {
    SPACE: 32,
    SHIFT: 16
};

let dt = 0;

const gameMap = new GameMap({
    playerID: 'player',
    canvas,
    vWidth,
    vHeight,
    radius: radius || MAP_RADIUS,
    pixelRatio: OS.pixelRatio
});
log(`Создан игровой мир с радиусом ${gameMap.radius}`);

const mouseCoords = {};

const deltaframe = new Deltaframe({
    targetFps: 120
});

log('Инициализация игры');
initGame();
deltaframe.start(draw);

/** dt
 * game init
 */
function initGame() {
    log('Инициализация слайзеров');
    gameMap.initUnits(numUnits || INIT_UNITS_COUNT);
    log(`Создано ${gameMap.units.length} слайзеров`);
    log('Инициализация еды');
    gameMap.initFoods(numFoods || INIT_FOODS_COUNT);
    log(`Создано ${gameMap.foodsOnField} единиц еды`);
    log('Биндинг глобальных листенеров');
    binding();
    log('Старт игрового процесса');
    gameMap.emit(Events.VISIBLE);
}

function draw(time, delta, deltaAverage) {
    stats.begin();
    dt = delta * MSSEC;
    gameMap.update(dt);
    gameMap.render(dt);
    stats.end();
}

/**
 * event binding
 */

function binding() {
    // finger|mouse move event
    if (navigator.userAgent.match(/(iPhone|iPod|Android|ios)/i)) {
        window.addEventListener('touchstart', mousemove);
        window.addEventListener('touchmove', mousemove);
    } else {
        // change snake's direction when mouse moving
        window.addEventListener('mousemove', mousemove);

        // speed up
        window.addEventListener('mousedown', () => {
            if (gameMap.player.length <= gameMap.player.minLength) return;

            gameMap.player.speedUp();
        });

        // speed down
        window.addEventListener('mouseup', () => {
            gameMap.player.speedDown();
        });

        window.addEventListener('keyup', e => {
            switch (e.keyCode) {
                case KeyCodes.SPACE:
                    gameMap.drawDebug = !gameMap.drawDebug;
                    log(`Draw debug: ${gameMap.drawDebug}`);
                    break;
                case KeyCodes.SHIFT:
                    gameMap.isScaled = !gameMap.isScaled;
                    gameMap.setToScale(gameMap.isScaled ? 2 : 1);
                    log(
                        `Scale game: ${gameMap.isScaled} from ${gameMap.scale} to ${
                            gameMap.toScale
                        }`
                    );
                    break;
                default:
                    break;
            }
        });
    }

    /* VisibilityHandler(gameMap);
    gameMap.on(Events.HIDDEN, onHidden);
    gameMap.on(Events.VISIBLE, onVisible); */

    function mousemove(e) {
        e.preventDefault();
        if (e.touches) {
            mouseCoords.x = e.touches[0].pageX;
            mouseCoords.y = e.touches[0].pageY;
        } else {
            const evt = e || window.event;
            mouseCoords.x = evt.clientX;
            mouseCoords.y = evt.clientY;
        }
        gameMap.player.toAngle = angleBetweenCoords(
            gameMap.player.x,
            gameMap.player.y,
            gameMap.view.absoluteX(mouseCoords.x),
            gameMap.view.absoluteY(mouseCoords.y)
        );
    }

    /* function onHidden() {
        deltaframe.pause();
        gameMap.emit(Events.PAUSE);
        log('Игра остановлена');
    }

    function onVisible() {
        deltaframe.resume();
        gameMap.emit(Events.RESUME);
        log('Игра запущена');
} */
}

function content(divSelector, value) {
    document.querySelector(divSelector).innerHTML = value;
}
