import FastList from 'fast-list';

export default class FList extends FastList {
    // eslint-disable-next-line no-useless-constructor
    constructor() {
        super();
    }

    every(fn, thisp) {
        let prev;
        let p = this._head;
        let i = 0;
        const len = this.length;
        let d = true;
        while (i < len && p && d) {
            d = fn.call(thisp || this, p.data, prev, i, this);
            prev = p.data;
            p = p.next;
            i += 1;
        }
    }

    getHead() {
        if (this._head) {
            return this._head.data;
        }
        // eslint-disable-next-line consistent-return
        return undefined;
    }

    getTail() {
        if (this._tail) {
            return this._tail.data;
        }
        // eslint-disable-next-line consistent-return
        return undefined;
    }
}
